.footer {
    background-color: var(--background-color);
}

.footer-wrapper {
    @media screen and (min-width:768px) {
        display: flex;
        justify-content: space-between;
    }
}


.footer-subtitle {
    font-weight: 700;
    font-size: 18px;
    line-height: 1.218;
    text-transform: uppercase;
    color: var(--text-light);
    text-align: left;
}

@media screen and (max-width:767px) {
    .footer-subtitle {
        max-width: 234px;
        margin-bottom: 16px;
    }
}

@media screen and (min-width:768px) {
    .footer-subtitle {
        font-size: 26px;
        letter-spacing: 0.04em;
        max-width: 352px;
        margin-bottom: 9px;
    }
}

@media screen and (min-width:1200px) {
    .footer-subtitle {
        font-size: 30px;
        font-weight: 400;
        letter-spacing: 0.04em;
        max-width: 504px;
        margin-bottom: 14px;
    }
}


.footer-title {
    font-weight: 700;
    font-size: 34px;
    line-height: 1.219;
    text-transform: uppercase;
    color: var(--text-light);
    text-align: left;
}


@media screen and (max-width:767px) {
    .footer-title {
        max-width: 200px;
    }
}

@media screen and (min-width:768px) {
    .footer-title {
        font-size: 48px;
        max-width: 340px;
        margin-bottom: 32px;
        letter-spacing: 0.04em;
    }
}

@media screen and (min-width:1200px) {
    .footer-title {
        font-size: 58px;
        max-width: 497px;
        margin-bottom: 54px;
    }
}


.footer-social__list {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 38.01px;
    margin-top: 42.01px;
    @media screen and (min-width:768px) {
            margin-top: 68px;
            margin-bottom: 32px;
        }
    @media screen and (min-width:1200px) {
            margin-top: 12px;
            margin-bottom: 40px;
        }
}

.footer-social__item:not(:last-child) {
    @media screen and (max-width:767px) {
    margin-right: 28px;}
    @media screen and (min-width:768px) {
    margin-right: 30px;
        }
}

.footer-social__link {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: var(--accent-2);
    color: var(--background-color);
    height: 32px;
    width: 32px;
    @media screen and (min-width:768px) {
                        height: 34px;
                        width: 34px;}
}

.footer-social__icon {
    fill: currentColor;
}

.footer__contact {
    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    color: var(--text-dark);
    text-align: right;
    padding-bottom: 40.01px;
    @media screen and (min-width:768px) {
        font-size:14px;
        width: 285px;
        }
    @media screen and (min-width:1200px) {
        font-size: 16px;
        width: 338px;
        letter-spacing: 0.02em;
        }
}

.footer__contact__link{
    color: var(--text-light);
}

.footer-rights {
    padding-top: 16px;
    border-top: solid 1px #E1E1E1;
    @media screen and (max-width: 480px) {
        padding-bottom: 62.01px;}
    @media screen and (min-width: 768px) {
        padding-top: 14px;
        }
    @media screen and (min-width: 1200px) {
        padding-top: 12px;
        padding-bottom: 11px;
        }
}

.rights-reserved {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    color: var(--text-copyright);
    max-width: 150px;
    text-align: center;
    margin: 0 auto;
    @media screen and (min-width: 768px) {
        letter-spacing: 0.02em;
        line-height: 1.86;
        max-width: 298px;
        }
    @media screen and (min-width: 1200px) {
        font-size: 12px;
        max-width: 255px;
        line-height: 1.86;
        }
}